@import url("https://fonts.googleapis.com/css2?family=Hind+Siliguri:wght@300;400;500;600;700&amp;display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&amp;display=swap");
@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@200;300;400;500&amp;display=swap");
/*================================================
1. Global CSS
=================================================*/
* {
  box-sizing: border-box;
  outline: none;
  -moz-osx-font-smoothing: grayscale;
  /* Firefox */
  -webkit-font-smoothing: antialiased;
  /* WebKit  */
}

body {
  padding: 0;
  margin: 0;
  font-family: "Hind Siliguri", sans-serif;
}

a {
  color: #F14705;
}

p {
  color: #666666;
  font-family: "Raleway", sans-serif;
  line-height: 26px;
}

h1, h2, h3, h4, h5, h6 {
  color: #353535;
}

.section-title h3 {
  text-transform: uppercase;
  font-weight: bold;
  margin-bottom: 30px;
  position: relative;
}
.section-title h3:after {
  content: "";
  position: absolute;
  width: 100px;
  height: 3px;
  background: #f14705;
  bottom: -10px;
  left: 0;
}

.section-title.text-center h3:after {
  left: 0;
  right: 0;
  margin: 0px auto;
}

.section-title h6 {
  margin: 10px 0;
}

button:focus {
  outline: none;
}

ul li {
  list-style: none;
}

.container {
  max-width: 1240px;
}

button {
  border: none;
  display: inline-block;
  padding: 8px 15px;
  border-radius: 5px;
  color: #fff;
  margin-top: 10px;
  background: #FFBD2E;
  transition: all 0.4s ease;
}
button:hover {
  background: #F14705;
}

input[type=text], input[type=email], input[type=phone] {
  padding: 8px 10px;
  border: 1px solid #d8d8d8;
  display: block;
  width: 100%;
  transition: all 0.4s ease;
}
input[type=text]:focus, input[type=email]:focus, input[type=phone]:focus {
  outline: none;
  border: 1px solid #F14705;
}

input[type=submit] {
  background: #FFBD2E;
  border: none;
  color: #fff;
  padding: 10px 20px;
  text-transform: uppercase;
  transition: all 0.4s ease;
}
input[type=submit]:focus {
  outline: none;
}
input[type=submit]:hover {
  background: #F14705;
}

table {
  width: 100%;
}
table tbody, table thead, table tfoot {
  width: 100%;
  border: 1px solid #f0f0f0;
  min-width: 100%;
}
table tbody tr td, table tbody tr th, table thead tr td, table thead tr th, table tfoot tr td, table tfoot tr th {
  border: 1px solid #ddd;
  padding: 10px;
}

span.required {
  color: red;
}

/*================================================
2. Bootstrap CSS
=================================================*/
.dropdown-menu {
  margin: 0;
  padding: 0;
  border: 1px solid #eee;
  box-shadow: 0 3px 12px rgba(27, 31, 35, 0.15), 0 0 1px rgba(27, 31, 35, 0.2);
  min-width: 13rem;
}

.dropdown-item {
  border-bottom: 1px solid #eee;
  font-size: 14px;
  padding: 0.5rem 0.6rem;
}
.dropdown-item i {
  width: 18px;
}
.dropdown-item:last-child {
  border-bottom: none;
}

.dropdown-item.active, .dropdown-item:active {
  background-color: #FFBD2E;
  color: #ffffff;
}

.btn-primary {
  background-color: #FFBD2E;
  border-color: #FFBD2E;
  color: #ffffff;
}
.btn-primary:hover {
  background-color: #F14705;
  border-color: #F14705;
}

.btn-secondary {
  background-color: #F14705;
  border-color: #F14705;
  color: #ffffff;
}

.form-control {
  height: calc(1.7em + .75rem + 2px);
}

.form-control:focus {
  box-shadow: none;
}

.navbar-light .navbar-toggler {
  margin-top: 0px;
  background: none;
}

.nav-tabs {
  justify-content: center;
}
.nav-tabs .nav-link {
  border-radius: 0px;
  justify-content: center;
}

.form-control {
  border-radius: 0;
}

.btn-primary:not(:disabled):not(.disabled).active, .btn-primary:not(:disabled):not(.disabled):active, .show > .btn-primary.dropdown-toggle {
  background-color: #F14705;
  border-color: #F14705;
}

.btn-primary:not(:disabled):not(.disabled).active:focus, .btn-primary:not(:disabled):not(.disabled):active:focus, .show > .btn-primary.dropdown-toggle:focus {
  box-shadow: none;
}

.pagination {
  margin: 25px 0;
}

.page-item.active .page-link {
  background-color: #F14705;
  border-color: #F14705;
}

.page-link {
  color: #353535;
  padding: 0.5rem 1rem;
  font-weight: 500;
  border: 1px solid rgba(0, 0, 0, 0.05);
  box-shadow: 0px 5px 15px 0px rgba(0, 0, 0, 0.08);
  margin: 5px;
  border-radius: 3px;
}
.page-link:hover {
  background-color: #FFBD2E;
  border-color: #FFBD2E;
  color: #fff;
}

blockquote.blockquote {
  background: #f8f8f8;
  padding: 20px;
  border-left: 4px solid #353535;
}

/*================================================
3. Mini Cart CSS
=================================================*/
.dropdown-menu.cart-dropdown.show {
  min-width: 25rem;
  padding: 20px;
  left: auto;
  right: 0;
}

.minicart-product-info img.img-border {
  border: 1px solid #eee;
  padding: 5px;
  width: 80px;
}
.minicart-product-info a {
  margin: 0;
  padding: 0 !important;
}
.minicart-product-info .product-meta h5.product-title {
  font-size: 15px;
  margin-top: 0px;
  line-height: 20px;
}
.minicart-product-info .product-meta h5.product-title a {
  padding: 0 !important;
  margin: 0 !important;
  text-transform: none;
}
.minicart-product-info .product-meta span.product-price {
  font-weight: bold;
}

.minicart-product-calculation {
  border-top: 1px solid #eee;
  margin-left: -20px;
  margin-right: -20px;
  padding: 20px 20px 0px 20px;
  margin-top: 20px;
}
.minicart-product-calculation p {
  margin-bottom: 5px;
}
.minicart-product-calculation .dcart-action a {
  color: #fff !important;
  margin: 3px;
  display: block;
  font-size: 16px;
  padding: 7px !important;
}

span.item-delete {
  padding: 1.5rem 0;
  display: block;
}

/*================================================
4. Header CSS
=================================================*/
.preloader {
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 99999;
  background: #fff;
  top: 0;
  left: 0;
}
.preloader .loader {
  position: absolute;
  top: 43%;
  left: 0;
  right: 0;
  -webkit-transform: translateY(-43%);
  transform: translateY(-43%);
  text-align: center;
  margin: 0 auto;
}

header.header-area {
  padding: 7px 0;
  margin: 0;
}
header.header-area.is-sticky {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 999;
  animation: 500ms ease-in-out 0s normal none 1 running fadeInDown;
  box-shadow: 0 2px 28px 0 rgba(0, 0, 0, 0.1);
  background: #ffffff;
}

