section.product-listing input[type=range]::-webkit-slider-runnable-track {
  width: 300px;
  height: 5px;
  background: #ddd;
  border: none;
  border-radius: 3px;
}
section.product-listing input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  border: none;
  height: 16px;
  width: 16px;
  border-radius: 50%;
  background: #FFBD2E;
  margin-top: -4px;
  cursor: pointer;
  position: relative;
  z-index: 1;
}
section.product-listing input[type=range]:focus {
  outline: none;
